<template>
  <div>
    <x-header class="titlebar" :left-options="leftOptions">
      <slot></slot>
    </x-header>
  </div>
</template>

<style lang="less">
  @import "../assets/styles/color.less";

  .titlebar {
    display: flex;
    color: #fff ;
    background-color: @active-blue !important;
    text-align: center;
    height: 44px;
    align-items: center;
    justify-content: center;

    .vux-header-back{
      color: #fff !important;
    }

    .vux-header-back:before {
      font-weight: bold;
      border: #fff 1px solid !important;
      border-width: 1px 0 0 1px !important;
    }
  }

</style>
<script>
  import XHeader from 'vux/x-header'

  export default {
    props: {
      leftOptions: {
        type: Object,
        default () {
          return {
            showBack: true,
            backText: '',
            preventGoBack: false
          }
        }
      }
    },
    components: {
      XHeader
    }
  }
</script>



