import {getWithinAuth} from '../../frame/ajax'
import {getUrl} from '../../frame/apiConfig'

/**
 * 下载 我的课程
 * @param dispatch
 * @returns {Promise}
 */
export const loadMyCourses = ({ dispatch }) => {
  return new Promise(
    (resolve, reject) => {
      getWithinAuth(
        {
          url: getUrl('my_course')
        }
      ).then(
        myCourses => {
          resolve()
          dispatch('UPDATE_MY_COURSES', myCourses)
        },
        err => {
          reject()
          console.warn(err)
        }
      )
    })
}



