<template>
  <div id="app">
    <router-view class="view" :transition="'vux-pop-' + (direction === 'forward' ? 'in' : 'out')" keep-alive></router-view>

    <!--bottom slot-->
    <tabbar class="vux-demo-tabbar" icon-class="vux-center" v-show="isTabbarView" slot="bottom" v-el:tab-bar>
      <tabbar-item v-link="{path:'/main'}" :selected="route.path === '/main'">
        <span class="demo-icon-22 vux-demo-tabbar-icon-home" slot="icon">&#xe637;</span>
        <span slot="label">Home</span>
      </tabbar-item>
      <tabbar-item v-link="{path:'/mycourse'}" :selected="isDemo" badge="9">
        <span class="demo-icon-22" slot="icon">&#xe633;</span>
        <span slot="label"><span v-if="componentName" class="vux-demo-tabbar-component">{{componentName}}</span><span v-else>Demos</span></span>
      </tabbar-item>
      <tabbar-item v-link="{path:'/setting'}" :selected="route.path === '/project/donate'" show-dot>
        <span class="demo-icon-22" slot="icon">&#xe630;</span>
        <span slot="label">Donate</span>
      </tabbar-item>
    </tabbar>

    <!--global-->
    <alert :show.sync="isAlert" button-text="知道了" class="alert">{{alertMsg}}</alert>
    <toast :show.sync="isToast" >{{toastMsg}}</toast>
    <confirm :show.sync="isConfirm" :title="confirmTitle" :confirm-text="confirmText" :cancel-text="cancelText"
             @on-confirm="onAction('confirm')" @on-cancel="onAction('cancel')">
      <p style="text-align:center;">{{{confirmMsg}}}</p>
    </confirm>
  </div>
</template>

<script>
  import store from './vuex/store'
  import {userActions, courseRecordActions} from './vuex/actions'
  import Alert from 'vux/alert'
  import Toast from 'vux/toast'
  import Confirm from 'vux/confirm'
  import {Tabbar, TabbarItem} from 'vux/tabbar'

  export default {
    store,

    vuex: {
      getters: {
        route: (state) => state.route,
        direction: (state) => state.direction
      },

      actions: {
        initUser: userActions.initUser,
        loadFreeRecords: courseRecordActions.loadAllFreeRecords,
        loadExpenseRecords: courseRecordActions.loadAllExpenseRecords
      }
    },

    data () {
      return store.state.global
    },

    created () {
      //初始化，加载用户
      this.initUser()

      //加载课程进度
      this.loadFreeRecords()
      this.loadExpenseRecords()
    },

    computed: {
      isTabbarView () {
        return this.route.path === '/main' || this.route.path === '/setting' || this.route.path === '/mycourse'
      }
    },

    methods: {
      onAction (type) {
        if (type === 'confirm') {
          this.confirmHandler()
        } else if (type === 'cancel') {
          this.cancelHandler()
        }
      }
    },

    components: {
      Alert,
      Toast,
      Tabbar,
      TabbarItem,
      Confirm
    }
  }
</script>

<style lang="less">
  @import "assets/styles/animate.less";
  html {
    height: 100%;
    font-family: "Microsoft YaHei" ! important;
    overflow-x: hidden;
  }

  body {
    display: flex;
    background: #f0eff5;
    /*align-items: center;*/
    /*justify-content: center;*/
    height: 100%;
    width: 100%;
    margin: 0;
  }

  #app {
    width: 100%;
    height: 100%;
  }


  /**
  * vue-router transition
  */
  .vux-pop-out-transition,
  .vux-pop-in-transition {
    width: 100%;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    backface-visibility: hidden;
  }
  .vux-pop-out-enter,
  .vux-pop-out-leave,
  .vux-pop-in-enter,
  .vux-pop-in-leave {
    will-change: transform;
    height: 100%;
    position: absolute;
    left: 0;
  }
  .vux-pop-out-enter {
    animation-name: popInLeft;
  }
  .vux-pop-out-leave {
    animation-name: popOutRight;
  }
  .vux-pop-in-enter {
    perspective: 1000;
    animation-name: popInRight;
  }
  .vux-pop-in-leave {
    animation-name: popOutLeft;
  }
  @keyframes popInLeft {
    from {
      transform: translate3d(-100%, 0, 0);
    }
    to {
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes popOutLeft {
    from {
      transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(-100%, 0, 0);
    }
  }
  @keyframes popInRight {
    from {
      transform: translate3d(100%, 0, 0);
    }
    to {
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes popOutRight {
    from {
      transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(100%, 0, 0);
    }
  }

</style>



